"use client";

import React from "react"

import { useState } from "react";
import { cn } from "@/lib/utils";
import {
  type DayData,
  type DotColor,
  type DayColorCode,
  WEEKDAYS,
  MONTH_NAMES_SHORT,
  BLOCK_CATEGORIES,
  formatTimeRange,
  DAY_COLORS,
} from "./calendar-types";
import { DayColorPicker } from "./day-color-picker";

interface WeekViewProps {
  weekStart: Date;
  getData: (year: number, month: number, day: number) => DayData;
  onUpdate: (year: number, month: number, day: number, data: DayData) => void;
  selectedDotColor: DotColor;
  onDayClick?: (date: Date) => void;
}

export function WeekView({
  weekStart,
  getData,
  onUpdate,
  selectedDotColor,
  onDayClick,
}: WeekViewProps) {
  const today = new Date();
  today.setHours(0, 0, 0, 0);

  const days = Array.from({ length: 7 }, (_, i) => {
    const d = new Date(weekStart);
    d.setDate(d.getDate() + i);
    return d;
  });

  return (
    <div className="h-full flex border-l border-t border-border">
      {days.map((date, i) => {
        const isToday = date.getTime() === today.getTime();
        const dayData = getData(date.getFullYear(), date.getMonth(), date.getDate());

        return (
          <WeekDayColumn
            key={i}
            date={date}
            data={dayData}
            isToday={isToday}
            onUpdate={(data) => onUpdate(date.getFullYear(), date.getMonth(), date.getDate(), data)}
            selectedDotColor={selectedDotColor}
            onDayClick={onDayClick}
          />
        );
      })}
    </div>
  );
}

interface WeekDayColumnProps {
  date: Date;
  data: DayData;
  isToday: boolean;
  onUpdate: (data: DayData) => void;
  selectedDotColor: DotColor;
  onDayClick?: (date: Date) => void;
}

function WeekDayColumn({
  date,
  data,
  isToday,
  onDayClick,
  onUpdate,
}: WeekDayColumnProps) {
  const [colorPickerPos, setColorPickerPos] = useState<{ x: number; y: number } | null>(null);
  
  const timeBlocks = data.timeBlocks || [];
  // Sort by start time
  const sortedBlocks = [...timeBlocks].sort((a, b) => a.startTime.localeCompare(b.startTime));
  
  // Get day color info
  const dayColorInfo = data.dayColor ? DAY_COLORS[data.dayColor] : null;

  const handleContextMenu = (e: React.MouseEvent) => {
    e.preventDefault();
    setColorPickerPos({ x: e.clientX, y: e.clientY });
  };

  const handleColorChange = (color: DayColorCode) => {
    onUpdate({ ...data, dayColor: color });
    setColorPickerPos(null);
  };

  return (
    <>
      <div 
        onContextMenu={handleContextMenu}
        className={cn(
          "flex-1 flex flex-col border-r border-border min-w-0",
          dayColorInfo && dayColorInfo.bgClass
        )}
      >
        {/* Day header */}
        <div
          onClick={() => onDayClick?.(date)}
          className={cn(
            "flex flex-col items-center py-2 border-b border-border cursor-pointer flex-shrink-0",
            "hover:bg-accent/30 transition-colors",
            !dayColorInfo && isToday && "bg-accent/50"
          )}
        >
          <span className="text-[10px] uppercase tracking-wider text-muted-foreground">
            {WEEKDAYS[date.getDay()]}
          </span>
          <span className={cn(
            "text-lg font-light",
            isToday ? "text-foreground" : "text-foreground/80"
          )}>
            {date.getDate()}
          </span>
          <span className="text-[9px] text-muted-foreground">
            {MONTH_NAMES_SHORT[date.getMonth()]}
          </span>
        </div>

        {/* Time blocks as cards */}
        <div className="flex-1 p-1.5 overflow-y-auto flex flex-col gap-1">
          {sortedBlocks.length > 0 ? (
            sortedBlocks.map((block) => {
              const cat = BLOCK_CATEGORIES[block.category];
              // Fallback for legacy categories
              if (!cat) {
                return (
                  <div
                    key={block.id}
                    onClick={() => onDayClick?.(date)}
                    className="px-2 py-1.5 rounded border-l-2 cursor-pointer hover:opacity-80 transition-opacity border-muted-foreground/50 bg-muted/50"
                  >
                    <div className="text-[10px] font-medium truncate">
                      {block.title}
                    </div>
                    <div className="text-[9px] text-muted-foreground">
                      {formatTimeRange(block.startTime, block.endTime)}
                    </div>
                  </div>
                );
              }
              return (
                <div
                  key={block.id}
                  onClick={() => onDayClick?.(date)}
                  className={cn(
                    "px-2 py-1.5 rounded border-l-2 cursor-pointer",
                    "hover:opacity-80 transition-opacity",
                    cat.bgClass,
                    cat.borderClass
                  )}
                >
                  <div className="text-[10px] font-medium truncate">
                    {block.title || cat.label}
                  </div>
                  <div className="text-[9px] text-muted-foreground">
                    {formatTimeRange(block.startTime, block.endTime)}
                  </div>
                </div>
              );
            })
          ) : (
            <div 
              onClick={() => onDayClick?.(date)}
              className="flex-1 flex items-center justify-center cursor-pointer hover:bg-accent/20 transition-colors rounded"
            >
              <span className="text-[10px] text-muted-foreground/50">No blocks</span>
            </div>
          )}
        </div>
      </div>
    </>
  );
}
